/* Token Label*/
export const requiredTokenLbl = () => {
    const myBody = document.getElementsByTagName("body")[0];
    const myBodyTr = myBody.getElementsByTagName("tr");
    const myTr = myBodyTr[0].firstChild;
    myTr.classList.add('tr-clk-class');
}

// /*Submit button*/
export const submitSetButton = () => {
    console.log('Creating submit button');
    const subButton = [...document.querySelectorAll('input[id*=submit][type=submit]')]?.shift();
    const tbodyEle = document.querySelector('form[class*=cl-stt] table tbody');
    if (subButton) {
        const newRowEle = document.createElement('tr');
        subButton.classList.remove('button');
        newRowEle.innerHTML = `<th scope="row"></th><td>
            <div class="cl-app-btn-wrapp--submit">
            ${subButton.outerHTML}
            </div>
        </td>`;
        tbodyEle.appendChild(newRowEle);
        subButton.parentElement.remove();
    };
}


